import React, { useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import cl from './HeaderTabs.module.scss';
// import useQuery from 'hooks/useQuery';

const HeaderTabs = () => {
	const [params, setParams] = useSearchParams()
  const tab = params.get('tab');

  useEffect(() => {
		// console.log(tab)
	}, [tab]);

	/******************* NEED TO DO ********************

	1. Link queries to global state (redux)

	2. Add state changing on tab change in useEffect

	3. Make default state (redux) for queries

	4. Repeat this in all comps, linked with queries
	
	***************************************************/
	const handleTabClick = e => setParams({tab: e.target.name})
	
  return (
    <ul className={cl.headerTabs}>
      <li>
        <button name='lineup' onClick={handleTabClick} className={tab === 'lineup' ? cl.active : ''}>
          Lineup
        </button>
      </li>
      <li>
        <button name='box' onClick={handleTabClick} className={tab === 'box' ? cl.active : ''}>
          Box
        </button>
      </li>
      <li>
        <button name='plays' onClick={handleTabClick} className={tab === 'plays' ? cl.active : ''}>
          Plays
        </button>
      </li>
      <li>
        <button name='videos' onClick={handleTabClick} className={tab === 'videos' ? cl.active : ''}>
          Videos
        </button>
      </li>
    </ul>
  );
};

export default HeaderTabs;
